export default {
  props: {
    label: {
      type: String,
      required: true
    },

    type: {
      type: String,
      default: 'text'
    },

    value: {
      required: true,
      type: [String, Number]
    }
  },

  computed: {
    component() {
      if (this.type === 'textarea') {
        return 'textarea'
      }

      return 'input'
    }
  }
}
